<template>
	<div class="course-list-box">
		<div class="tool-row">
			<div class="layui-btn layui-btn-normal layui-btn-sm" @click="add"></div>
		</div>
		<layui-table :element.sync="table" v-bind="$TableConfig.GET_ALL_CLASS_TABLE" @tool="TableTool" />
	</div>
</template>

<script>
	export default {
		name: 'AdminLookClass',
		data() {
			return {
				table: null,
			}
		},
		methods: {
			add() {
				this.$prompt('', '', {
					confirmButtonText: '',
				}).then(({ value }) => {
					if(value === null) {
						this.$ShowToast('');
						return;
					}
					this.$Post(this.$Api.ADD_CLASS, {
						classNumber: value
					}).then(res => {
						if(res.code == 0) {
							this.$ShowToast('');
							this.table.reloadTable()
						} else {
							this.$ShowToast(res.msg)
						}
					})
				}).catch(() => {})
			},
			TableTool({ event, data }) {
				if(event == 'edit') {
					this.$prompt('', '', {
						confirmButtonText: '',
						inputValue: data.classNumber
					}).then(({ value }) => {
						if(value === null) {
							this.$ShowToast('');
							return;
						}
						this.$Post(this.$Api.CHANGE_CLASS_INFO, {
							classId: data.classId,
							classNumber: value
						}).then(res => {
							if(res.code == 0) {
								this.$ShowToast('');
								this.table.reloadTable()
							} else {
								this.$ShowToast(res.msg)
							}
						})
					}).catch(() => {})
				}
			}
		}
	}
</script>

<style lang="stylus" scoped>
	.course-list-box
		background: #fff;
		padding: 10px 17px;
		border-radius: 3px;
		overflow: hidden;
</style>


